import "./style.css";

export const imgModal = () => {
    const imagesBox = document.querySelectorAll(".c-img-box");
    const imagesBoxImg = document.querySelectorAll(".c-img-box img");

    imagesBox.forEach((img, id) => {

        const imagesSrc = img.querySelector(".c-img-box img"),
            imgMod = document.createElement("div"),
            imgModBg = document.createElement("div"),
            image = document.createElement("img"),
            closeBtn = document.createElement("button"),
            prevBtn = document.createElement("button"),
            nextBtn = document.createElement("button");

        closeBtn.setAttribute("class", "close-btn");
        closeBtn.setAttribute("data-crsr-zoomout", true);
        prevBtn.setAttribute("class", "prev-btn");
        nextBtn.setAttribute("class", "next-btn");
        imgMod.setAttribute("class", "img-modal");
        imgModBg.setAttribute("class", "img-modal-bg");

        img.addEventListener("click", openWindow);
        imgMod.addEventListener("click", clodeWindow);
        closeBtn.addEventListener("click", clodeWindow);
        image.addEventListener('contextmenu', e => e.preventDefault());

        function openWindow() {
            image.src = imagesSrc.getAttribute('src');
            image.setAttribute("class", `active`);
            image.setAttribute("alt", `image-${id}`);

            console.log(id.toString())

            document.body.appendChild(imgMod);
            imgMod.appendChild(closeBtn);
            // imgMod.appendChild(prevBtn);
            // imgMod.appendChild(nextBtn);
            imgMod.appendChild(image);
            imgMod.appendChild(imgModBg);
            setTimeout(() => imgMod.classList.add("active"), 10);

            // prevBtn.addEventListener("click", () => {
            //     --id;
            //     if (id === -1) {
            //         console.log("ops");
            //         console.log("prevBtn: ", id);
            //         imgMod.setAttribute("data", imgMod.src)
            //     }
            // })
            // nextBtn.addEventListener("click", () => {
            //     ++id;
            //     if (id > imagesBox.length) {
            //         console.log("ops", id);
            //     }
            //     console.log("nextBtn: ", id);
            // })
        }

        function clodeWindow() {
            imgMod.classList.remove("active");
            setTimeout(() => {
                imgMod.remove();
                closeBtn.remove();
                // prevBtn.remove();
                // nextBtn.remove();
                image.remove();
                imgModBg.remove();
            }, 400);
        }
    })

}