import { isBrowser } from "react-device-detect";
import "./style.css";

export const vPlayer = () => {

    const videoPlayer = document.querySelectorAll(".v--plr");

    videoPlayer.forEach(vPlayer => {
        const video = vPlayer.querySelector(".vd"),
            playBtn = vPlayer.querySelector(".play-btn"),
            progress = vPlayer.querySelector(".prgrs"),
            progressBar = vPlayer.querySelector(".prgrs-filled"),
            controls = vPlayer.querySelector(".cntrs"),
            controlsBgBtn = vPlayer.querySelector(".cntrsbg-btn"),
            loopbtn = vPlayer.querySelector(".loop-btn"),
            fScreenbtn = vPlayer.querySelector(".fscr-btn"),
            volume = vPlayer.querySelector(".vol-btn"),
            posterImg = vPlayer.querySelector('.v--plr img'),
            tittleVd = vPlayer.querySelector('.v--plr p');

        vPlayer.addEventListener("mousemove", () => controls.classList.add("active"));
        vPlayer.addEventListener("dblclick", () => { fscreenState = !fscreenState; (fscreenState) ? openFullscreen() : closeFullscreen() });
        playBtn.addEventListener("click", togglePlay);
        controlsBgBtn.addEventListener("click", togglePlay);
        loopbtn.addEventListener("click", handleLoop);
        fScreenbtn.addEventListener("click", () => { fscreenState = !fscreenState; (fscreenState) ? openFullscreen() : closeFullscreen() });
        video.addEventListener("click", togglePlay);
        video.addEventListener("play", updateToggleButton);
        video.addEventListener("pause", updateToggleButton);
        video.addEventListener("timeupdate", handleProgress);
        progress.addEventListener("click", scrub);
        progress.addEventListener("mousedown", () => (mousedown = true));
        progress.addEventListener("mousemove", (e) => mousedown && scrub(e));
        progress.addEventListener("mouseup", () => (mousedown = false));
        volume.addEventListener("change", handleVolumeUpdate);

        video.volume = 0.7; // default volume
        video.volume === 0.7 ? volume.value = "0.7" : volume.value = "1";

        if (videoPlayer) {  // contextmenu on/off
            vPlayer.addEventListener('contextmenu', e => e.preventDefault());
        }

        let hideControls;
        let fscreenState;
        let mousedown = false;

        function togglePlay() {
            if (isBrowser) {
                if (video.paused || video.ended) {
                    video.play();
                    controlsOff();
                } else {
                    video.pause();
                    controlsOn();
                }
            } else {
                if (video.paused || video.ended) {
                    video.play();
                    controlsMobileOff()
                } else {
                    video.pause();
                    controlsMobileOn()
                }
            }

        }

        function controlsOn() {
            controls.classList.add("active");
            tittleVd.classList.remove("off");
            controlsBgBtn.classList.remove("off");
            vPlayer.addEventListener("mousemove", () => clearTimeout(hideControls));
        }

        function controlsOff() {
            tittleVd.classList.add("off");
            controlsBgBtn.classList.add("off");
            posterImg.classList.add("off");
            hideCntrls();
            vPlayer.addEventListener("mousemove", () => {
                clearTimeout(hideControls);
                hideCntrls();
            });
        }

        function hideCntrls() {
            hideControls = setTimeout(() => {
                controls.classList.remove("active");
            }, 200);
        }

        function controlsMobileOn() {
            controls.classList.add("active");
            tittleVd.classList.remove("off");
        }

        function controlsMobileOff() {
            tittleVd.classList.add("off");
            controlsBgBtn.classList.add("off");
            posterImg.classList.add("off");
            controls.classList.remove("active");
        }

        function updateToggleButton() {
            playBtn.innerHTML = !video.paused ? "❚❚" : "►";
        }

        function handleProgress() {
            const progressPercentage = (video.currentTime / video.duration) * 100;
            progressBar.style.flexBasis = `${progressPercentage}%`;
            if (progressPercentage === 100) {
                controlsOn();
                posterImg.classList.remove("off");
            }
        }

        function scrub(e) {
            const scrubTime = (e.offsetX / progress.offsetWidth) * video.duration;
            video.currentTime = scrubTime;
            posterImg.classList.add("off");
        }

        function handleVolumeUpdate() {
            video[this.name] = this.value;
            if (volume.value === "0") {
                console.log("volume off")
            } else if (volume.value >= "0.05") {
                console.log("volume on")
            }
        }

        function handleLoop() {
            video.toggleAttribute('loop');
            loopbtn.classList.toggle('active')
        }

        function openFullscreen() {
            if (vPlayer.requestFullscreen) {
                vPlayer.requestFullscreen();
            } else if (vPlayer.webkitRequestFullscreen) { /* Safari */
                vPlayer.webkitRequestFullscreen();
            } else if (vPlayer.msRequestFullscreen) { /* IE11 */
                vPlayer.msRequestFullscreen();
            }
        }

        function closeFullscreen() {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.webkitExitFullscreen) { /* Safari */
                document.webkitExitFullscreen();
            } else if (document.msExitFullscreen) { /* IE11 */
                document.msExitFullscreen();
            }
        }

    });

}
