import React from "react";
import { Link } from "react-router-dom";
import { isBrowser, isMobile } from "react-device-detect";
import { useDataJson, styleDataJson, blogItems } from "../../../hooks/useDataJson";

import { HoverCursor } from "../../../components/CustomCursor/CursorHoverSeiitngs";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import { ScrollBlock } from "../../../hooks/ScrollBlock";

import { imgModal } from "../../../components/imagesModal/imgModal";
import { vPlayer } from "../../../components/vplayer/VPlayer";

gsap.registerPlugin(ScrollTrigger);


const imgPreload = () => {
  const imgCont = document.querySelectorAll('.c-img-box img');
  if (imgCont) {
    imgCont.forEach(i => {
      const loaded = () => {
        i.classList.add('img-loaded');
      }
      if (i.complete) {
        loaded()
      } else {
        i.addEventListener('load', loaded)
        i.addEventListener('error', function () {
          console.log('error image url:', i)
        })
      }
    })
  }
};


const animaHeaderElements = () => {

  // ----------- animation header elements

  HoverCursor();

  const imageHeaderAnim = document.querySelector('.img-cont'),
    imgContAnim = document.querySelector('.img-cont-anim'),
    titleHeaderAnim = document.querySelector('.title-header-anim'),
    categoryCont = document.querySelector('.category'),
    pageContent = document.querySelector('.page-content'),
    contScrll = document.querySelectorAll('.cont-scrll'),
    contImgprlx = document.querySelector('.cont-img-prlx'),
    contImgprlxImg = document.querySelector('.cont-img-prlx img'),
    // vPlayer = document.querySelectorAll('.v-player'),
    contScrllTxt = document.querySelectorAll('.cont-scrll-txt'),
    contentPagination = document.querySelector('.content-pagination');

  if (imageHeaderAnim) {

    // if (isBrowser) {
    //   if (vPlayer) {
    //     vPlayer.forEach(i => {
    //       i.addEventListener('contextmenu', e => e.preventDefault());
    //       i.addEventListener('click', () => i.classList.add('act'));
    //     });
    //   }
    // }
    // if (isMobile) {
    //   if (vPlayer) {
    //     vPlayer.forEach(i => {
    //       i.addEventListener('contextmenu', e => e.preventDefault());
    //       i.classList.add('act');
    //     });
    //   }
    // }


    setTimeout(() => {
      gsap.to(imageHeaderAnim, 4, { scale: 1, ease: "power2" });
      gsap.to(imgContAnim, .76, { height: '50vh', ease: "sine.out", delay: .8 });
    }, 200)

    setTimeout(() => {
      pageContent.style.cssText = `visibility: hidden; opacity: 0`;
      titleHeaderAnim.style.opacity = 0;
      gsap.fromTo(categoryCont, 1, { opacity: 0 }, { opacity: 1, ease: "sine.out", delay: 1 });
      gsap.to(pageContent, .8, { visibility: 'visible', opacity: 1, ease: "sine.out", delay: 1.5 });
      gsap.fromTo(titleHeaderAnim, 1.2, { x: '10%', opacity: 0 }, { x: '0', opacity: 1, ease: "power2.out", delay: 1 });

      gsap.to(imageHeaderAnim, {
        scrollTrigger: {
          trigger: ".sth-scrl",
          scroller: ".sth-scrl",
          scrub: true,
          start: "top top",
          end: "+=100%",
          ease: "none"
        }, y: "20vh"
      });

      contScrll.forEach(i => {
        if (isBrowser) {
          i.style.cssText = `transform: translateY(30%)`;
        } else {
          i.style.cssText = `transform: translateY(40%)`;
        }
        gsap.to(i, .7, {
          scrollTrigger: {
            trigger: i,
            scroller: ".sth-scrl",
            start: "top bottom",
            ease: "sine.out"
          }, transform: 'translateY(0)'
        });
      });

      contScrllTxt.forEach(tex => {
        (isBrowser) ? tex.style.cssText = `transform: translateX(15%); opacity:0` : tex.style.cssText = `transform: translateX(15%); opacity:0`;
        // tex.style.cssText = `transform: translateX(15%); opacity:0`;
        gsap.to(tex, .9, {
          scrollTrigger: {
            trigger: tex,
            scroller: ".sth-scrl",
            start: "top bottom",
            ease: "sine.out"
          }, transform: 'translateX(0)', opacity: 1
        });
      })

      contentPagination.style.cssText = 'transform: translateY(5vw)';
      gsap.to(contentPagination, .7, {
        scrollTrigger: {
          trigger: contentPagination,
          scroller: ".sth-scrl",
          start: "top bottom",
          ease: "sine.out"
        }, transform: 'translateY(0)'
      });

      if (contImgprlx || contImgprlxImg) {
        gsap.to(contImgprlxImg, {
          scrollTrigger: {
            trigger: contImgprlx,
            scroller: ".sth-scrl",
            start: "top bottom",
            scrub: true,
            end: "+=300%",
            ease: "sine.out"
          }, y: '40%', scale: '1'
        }, '-=6');
      }

      ScrollTrigger.refresh();

    }, 800)
  };
}

export function Item({ id }) {

  const [itemsData, isLoaded, error] = useDataJson(blogItems.url);

  ScrollBlock(3000);

  // ----------- Page navigation

  return (
    <>
      {(error) ? <div className="l--data" style={styleDataJson.loadError}><h4>Error: {error.message}</h4></div> : (!isLoaded) ? <div style={styleDataJson.loadError}><h4>Loading...</h4></div> :
        <>
          <div className="card-content-container" style={{ marginTop: '50vh' }}>
            {/* <div className="img-cont-anim-back"></div> */}
            <div className="img-cont-anim">
              {/* <Link className="link-img-cont" to="/blog/" data-crsr-back /> */}
              <div className="img-cont" style={{ backgroundImage: `url(/testing/assets/img/${id}.jpg)` }} loading="lazy" />
            </div>
            {itemsData.map(item => {
              const { category, title, content } = item;

              if (item.id === id) {
                const prevNextCss = { position: 'absolute', pointerEvents: 'none', display: 'none' };

                const pageSrcPrev = (id.substr(4) * 1) + 1;
                const pageSrcNext = (id.substr(4) * 1) - 1;

                const backBtnOff = () => {
                  const backBtn = document.querySelector(".back--btn");
                  backBtn.style.cssText = "display:none; pointer-events: none"
                }

                // const pagePrev = () => (pageSrcPrev > itemsData.length) ? `` : `p-n/p-00${pageSrcPrev}`;
                // const pageNext = () => (pageSrcNext === 0) ? `` : `p-n/p-00${pageSrcNext}`;

                return (
                  <div key={id} ref={() => { imgPreload(); animaHeaderElements(); vPlayer(); imgModal();}}>
                    <div className="container" style={{ paddingTop: 'calc(35px + 4vw)', paddingBottom: '0' }}>
                      <span className="category">{category}</span>
                      <h2 className="title-header-anim" style={{ margin: 'calc(40px + 4vw) 0' }}>{title}</h2>
                    </div>
                    <div className="page-content" dangerouslySetInnerHTML={{ __html: content }} />
                    <div className="container" style={{ paddingTop: 0 }}>
                      <div className="page-content">
                        <div className="content-pagination">
                          {(item.id === id) ? ((pageSrcPrev > itemsData.length) ? <Link className="back--btn-on" to="/works/" data-crsr-back ref={() => backBtnOff()}><h2>works</h2></Link> :
                            <Link className="prev--btn" to={`/works/p-n/p-00${pageSrcPrev}`} data-crsr-prev><h2>PREV</h2><div style={prevNextCss}><img src={`/testing/assets/img/p-00${pageSrcPrev}.jpg`} alt="Sample" /></div></Link>) : null}
                          <Link className="back--btn" to="/works/" data-crsr-back><h4>works</h4></Link>
                          {(item.id === id) ? ((pageSrcNext === 0) ? <Link className="back--btn-on" to="/works/" data-crsr-back ref={() => backBtnOff()}><h2>works</h2></Link> :
                            <Link className="next--btn" to={`/works/p-n/p-00${pageSrcNext}`} data-crsr-next><h2>NEXT</h2><div style={prevNextCss}><img src={`/testing/assets/img/p-00${pageSrcNext}.jpg`} alt="Sample" /></div></Link>) : null}
                        </div>
                      </div>
                    </div>
                  </div>
                )
              }
              return null
            })}
          </div>
        </>
      }
    </>
  )
}
